/* eslint-disable react/jsx-props-no-spreading */
import * as React from 'react';
import { styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import { OutlinedInputProps } from '@mui/material/OutlinedInput';

const red = '#d32f2f';

const RedditTextField = styled((props: any) => (
  <TextField
    InputProps={{ disableUnderline: true, endAdornment: props.endAdornment } as Partial<OutlinedInputProps>}
    {...props}
  />
), {
  shouldForwardProp: (prop:string) => prop !== 'error',
})(({ theme, error }) => ({
  '& .MuiFilledInput-root': {
    border: `1px solid ${error ? red : '#e2e2e1'}`,
    overflow: 'hidden',
    borderRadius: 4,
    backgroundColor: '#ffffff',
    transition: theme.transitions.create([
      'border-color',
      'background-color',
      'box-shadow',
    ]),
    '&:hover': {
      backgroundColor: 'transparent',
    },
    '&.Mui-focused': {
      backgroundColor: 'transparent',
      // borderColor: theme.palette.primary.main,
      color: error ? red : theme.palette.primary.main,
      borderColor: error ? red : theme.palette.primary.main,
    },
  },
  '& .MuiFormHelperText-root': {
    color: error ? red : theme.palette.primary.main,
  },
  '& .MuiInputBase-input': {
    color: error ? red : 'rgba(33, 33, 33, 0.6)',
  },

  '& .MuiInputLabel-root': {
    color: error ? red : '',

  },
  '& .MuiInputLabel-root.Mui-focused': {
    color: error ? red : theme.palette.primary.main,
  },

}));

export default function InputField(props:any) {
  const { ...rest } = props;
  return (

    <RedditTextField
      variant='filled'
      fullWidth
      style={{ marginBottom: 20 }}
      {...rest}
    />

  );
}
