/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { useState, useEffect } from 'react';
import { NavLink, Link } from 'react-router-dom';
import { IconButton, Avatar, Popover } from '@mui/material';
import { useDispatch } from 'react-redux';
import Logo from '../../assests/images/Logo.png';
import PrimayButton from '../../shared-components/components/PrimayButton';
import { logout } from '../../client/login/store';

export default function DesktopNav(props:any) {
  const dispatch = useDispatch();
  const { user } = props;

  const [anchorEl, setAnchorEl] = useState(null);
  const [buildingPopup, setBuildingPopup] = useState(null);

  const handleClick = (event:any) => {
    setAnchorEl(event.currentTarget);
  };
  const buildingHandleClick = (event:any) => {
    setBuildingPopup(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const buildingHandleClose = () => {
    setBuildingPopup(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;
  const buildingOpen = Boolean(buildingPopup);
  const buildingId = buildingOpen ? 'simple-popover-building' : undefined;

  return (
    <nav className='navbar-container'>
      <div className='ad-main-navbar container'>

        {/* Left side ==> */}
        <div className='left-side-nav'>
          <Link to='/' className='logo-wrapper'>
            <img src={Logo} alt='strada-logo' />
          </Link>

          <div className='client-nav-links'>
            <NavLink
              to='/learn-more'
              className={({ isActive }) => (isActive ? 'nav-link p-2 activeLinkClass ' : 'nav-link p-2 ')}
            >
              Buildings

            </NavLink>
            <i onClick={buildingHandleClick} className='fa-solid fa-caret-down' />

            <Popover
              id={buildingId}
              open={buildingOpen}
              anchorEl={buildingPopup}
              onClose={buildingHandleClose}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
            >

              <div className='setting-popver'>
                <Link to='/setting' className='setting-option'> Building One </Link>
                <div onClick={() => dispatch(logout())} className='setting-option'> Building Two </div>
              </div>

            </Popover>
            <NavLink
              to='/contact-us'
              className={({ isActive }) => (isActive ? 'nav-link p-2 activeLinkClass' : 'nav-link p-2')}
            >
              Dashboard
            </NavLink>
            <PrimayButton component={Link} to='/requestdemo'> Create Building </PrimayButton>

          </div>
        </div>

        <div className='right-side-nav'>
          <IconButton>
            <i className='fa-solid fa-bell' />
          </IconButton>

          <IconButton onClick={handleClick}>
            <Avatar src={`${process.env.REACT_APP_IMAGE_URL}${user?.avatar}`}>
              {' '}
              {user?.first_name}
              {' '}
            </Avatar>

          </IconButton>

          <Popover
            id={id}
            open={open}
            anchorEl={anchorEl}
            onClose={handleClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'left',
            }}
          >

            <div className='setting-popver'>
              <Link to='/setting' className='setting-option'> Account Seeting </Link>
              <div onClick={() => dispatch(logout())} className='setting-option'> Logout </div>
            </div>

          </Popover>

        </div>

      </div>
    </nav>
  );
}
