import React from 'react';
import './_requestDemo.scss';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
import PrimayButton from '../../shared-components/components/PrimayButton';
import HookTextField from '../../shared-components/hooks/HookTextField';

type defaultValues = {
    first_name: string;
    email: string;
}

const defaultValues = {
  first_name: '',
  email: '',
};

const schema = yup.object().shape({
  first_name: yup.string().required('Please enter your first name'),
  email: yup.string().required('Please enter your email').email('Your Email is not valid').min(6, 'Email length can not be less then 6 charecters'),
});

export default function RequestDemo() {
  const { enqueueSnackbar } = useSnackbar();
  const {
    control, formState, handleSubmit, reset,
  } = useForm({
    mode: 'onChange',
    defaultValues,
    resolver: yupResolver(schema),
  });

  const { errors, isValid } = formState;

  const onSubmit = (data: defaultValues) => {
    enqueueSnackbar('Login Successfully', { variant: 'success' });
    console.log({ data });
    reset(defaultValues);
  };

  return (
    <div className='full-width demo-container'>
      <div className='demo-content full-height flex-col-center'>

        <h1> Request Demo </h1>
        <p className='p-1 text-center-md'> Thanks for your interest! Please enter your e-mail and we&apos;ll be in contact shortly. </p>

        <form onSubmit={handleSubmit(onSubmit)}>

          <HookTextField
            name='first_name'
            label='First Name'
            control={control}
            errors={errors}
            type='text'
          />
          <HookTextField
            name='email'
            label='Email'
            control={control}
            errors={errors}
            type='text'
          />

          <PrimayButton type='submit' disabled={!isValid}> Request a Demo </PrimayButton>

        </form>

      </div>

    </div>
  );
}
