import React from 'react';
import './_forgetPassword.scss';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
import { Link } from 'react-router-dom';
import { useMutation } from 'react-query';
import axios from 'axios';
import PrimayButton from '../../shared-components/components/PrimayButton';
import HookTextField from '../../shared-components/hooks/HookTextField';
import StradaLoader from '../../shared-components/components/StradaLoader';

type defaultValues = {
    email: string;
}

const defaultValues = {
  email: '',
};

const schema = yup.object().shape({
  email: yup.string().required('Please enter your email').email('Your Email is not valid').min(6, 'Email length can not be less then 6 charecters'),
});

export default function ForgetPassword() {
  const { enqueueSnackbar } = useSnackbar();
  const {
    control, formState, handleSubmit, reset,
  } = useForm({
    mode: 'onChange',
    defaultValues,
    resolver: yupResolver(schema),
  });

  const { errors, isValid } = formState;

  const handleForgetPasssword = (data: defaultValues) => axios({
    url: 'api/auth-users/forgot-password/',
    method: 'post',
    data,
  });

  const results: any = useMutation(handleForgetPasssword, {
    onSuccess: () => {
      reset(defaultValues);
      enqueueSnackbar('Reset link sent to your email, check your email', { variant: 'success' });
    },
    onError: () => {
      enqueueSnackbar(results.error?.message ?? 'Problem in reset password', { variant: 'error' });
    },
  });

  const { mutate, isLoading } = results;

  const onSubmit = (data: defaultValues) => {
    mutate(data);
  };

  return (
    <div className='full-width login-container '>
      <div className='login-content full-height flex-col-center'>
        <StradaLoader open={isLoading} />
        <h1> Reset Password</h1>
        <p className='p-1 text-center-md'> Enter the email address associated with your account and we&apos;ll send you a link to reset your password.</p>

        <form onSubmit={handleSubmit(onSubmit)}>

          <HookTextField
            name='email'
            label='Email'
            control={control}
            errors={errors}
            type='text'
          />

          <PrimayButton type='submit' disabled={!isValid}> Send Reset Instructions </PrimayButton>
          <div style={{ marginTop: 20 }} className='text-center'>
            <Link to='/signin' className='link-button'> Return to Login </Link>
          </div>

        </form>

      </div>

    </div>
  );
}
