import React from 'react';
import { Grid, Box } from '@mui/material';
import './_dualColumnsImage.scss';

type props = {
  text:string;
  heading:string;
  type:string;
  src:string;
  brief:string;
}
export default function DualColumnsVideo(props: props) {
  const {
    text,
    heading,
    src,
    type,
    brief,
  } = props;
  return (
    <Grid container className='container dual-column-image-block'>
      { type === 'left' ? (
        <>
          <Grid item lg={6} sm={12} className='content-column'>
            <Box>
              <p className='intro-brief'>{brief}</p>
              <p className='intro-slogan text-center-md'>{heading}</p>
              <p className='intro-desc text-center-md'>
                {text}
              </p>
            </Box>
          </Grid>
          <Grid item lg={6} sm={12} className='image-column-right'>
            <img src={src} alt='' />

          </Grid>
        </>
      ) : (
        <>
          <Grid item lg={6} sm={12} className='image-column-left'>
            <img src={src} alt='' />
          </Grid>
          <Grid item lg={6} sm={12} className='content-column'>
            <Box>
              <p className='intro-brief'>{brief}</p>
              <p className='intro-slogan text-center-md'>{heading}</p>
              <p className='intro-desc text-center-md'>
                {text}
              </p>
            </Box>
          </Grid>
        </>
      ) }
    </Grid>
  );
}
