import React from 'react';
import './_signup.scss';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
import { useMutation } from 'react-query';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import PrimayButton from '../../shared-components/components/PrimayButton';
import HookTextField from '../../shared-components/hooks/HookTextField';
import StradaLoader from '../../shared-components/components/StradaLoader';

type defaultValues = {
    first_name: string;
    last_name: string;
    email: string;
    password: string;
    repeat_password: string;
}

const defaultValues = {
  first_name: '',
  last_name: '',
  email: '',
  password: '',
  repeat_password: '',
};

const schema = yup.object().shape({
  first_name: yup.string().required('Please enter your first name'),
  last_name: yup.string().required('Please enter your password'),
  email: yup.string().required('Email is required').email('Please enter valid email'),
  password: yup.string().required('Please enter your password'),
  repeat_password: yup.string()
    .oneOf([yup.ref('password'), null], 'Passwords must match'),
});

export default function Signup() {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();
  const {
    control, formState, handleSubmit, reset,
  } = useForm({
    mode: 'onChange',
    defaultValues,
    resolver: yupResolver(schema),
  });

  const { errors, isValid } = formState;

  const signupUser = (data:defaultValues) => axios({
    url: 'api/auth-users/sign-up/',
    method: 'post',
    data,
  });

  const results : any = useMutation(signupUser, {
    onSuccess: () => {
      reset(defaultValues);
      enqueueSnackbar('Account Created Successfully. Login !', { variant: 'success' });
      navigate('/signin');
    },
    onError: () => {
      enqueueSnackbar(results.error?.detail[0] ?? 'Signup Failed', { variant: 'error' });
    },
  });

  const {
    mutate, isLoading, isError, error,
  } = results;

  const onSubmit = (data: defaultValues) => {
    mutate(data);
  };

  return (
    <div className='full-width signup-container '>
      <div className='signup-content full-height flex-col-center'>

        <StradaLoader open={isLoading} />

        <h1> Create Account</h1>

        <form onSubmit={handleSubmit(onSubmit)}>

          <HookTextField
            name='first_name'
            label='First Name'
            control={control}
            errors={errors}
          />
          <HookTextField
            name='last_name'
            label='Last Name'
            control={control}
            errors={errors}
          />

          <HookTextField
            name='email'
            label='Email'
            control={control}
            errors={errors}
            type='text'
          />
          <HookTextField
            name='password'
            label='Password'
            control={control}
            errors={errors}
            type='password'
          />
          <HookTextField
            name='repeat_password'
            label='Confirm Password'
            control={control}
            errors={errors}
            type='password'
          />

          <PrimayButton type='submit' disabled={!isValid}> Get Started </PrimayButton>

        </form>

      </div>

    </div>
  );
}
