import React, { useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import Logo from '../../assests/images/Logo.png';
import SecondaryButton from '../../shared-components/components/SecondaryButton';

export default function MobileNav() {
  const [open, setOpen] = useState(false);
  return (
    <nav className='navbar-container'>
      <div className='main-navbar container'>

        {/* Left side ==> */}
        <div className='left-side-nav'>
          <Link to='/' className='logo-wrapper'>
            <img src={Logo} alt='strada-logo' />
          </Link>
        </div>

        <div className='right-side-nav'>
          <div className='client-nav-links' />
          {open ? <i className='fa-solid fa-xmark' onClick={() => setOpen(false)}></i> : <i className='fa-solid fa-bars-staggered' onClick={() => setOpen(true)}></i>}

        </div>
      </div>

      {open ? (
        <div className='mobile-list'>
          <div className='mobile-list-wrapper container'>
            <NavLink
              to='/learn-more'
              onClick={() => setOpen(false)}
              className={({ isActive }) => (isActive ? 'nav-link p-2 activeLinkClass' : 'nav-link p-2')}
            >
              Learn More
            </NavLink>
            <NavLink
              to='/contact-us'
              onClick={() => setOpen(false)}
              className={({ isActive }) => (isActive ? 'nav-link p-2 activeLinkClass' : 'nav-link p-2')}
            >
              Contact Us
            </NavLink>
            <NavLink
              to='/signin'
              onClick={() => setOpen(false)}
              className={({ isActive }) => (isActive ? 'nav-link p-2 activeLinkClass' : 'nav-link p-2')}
            >
              Login
            </NavLink>
            <SecondaryButton style={{ marginTop: '12rem' }} fullWidth component={Link} to='/requestdemo'> Request Demo </SecondaryButton>

          </div>
        </div>
      ) : ''}

    </nav>
  );
}
