import React from 'react';
import './_contactUs.scss';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
import { Grid } from '@mui/material';
import PrimayButton from '../../shared-components/components/PrimayButton';
import HookTextField from '../../shared-components/hooks/HookTextField';
import HookSelectField from '../../shared-components/hooks/HookSelectField';
import CustomCheckbox from '../../shared-components/inputs/CustomCheckbox';

const dummyOptions = [
  {
    name: 'Pakistan',
    value: 1,
  },
  {
    name: 'Maldeves',
    value: 2,
  },
];

type defaultValues = {
  first_name: string;
  last_name: string;
  email: string;
  phone: string;
  country: number;
  company: string;
  message: string;
  isAggreed: boolean;
}

const defaultValues = {
  first_name: '',
  last_name: '',
  email: '',
  phone: '',
  country: '',
  compnay: '',
  message: '',
  isAggreed: false,
};

const schema = yup.object().shape({
  first_name: yup.string().required('Please enter your first name'),
  last_name: yup.string().required('Please enter your last name'),
  email: yup.string().required('Please enter your email').email('Your email enter a valid email'),
  phone: yup.string().required('Please enter your phone number'),
  country: yup.number().required('Please select your country'),
  message: yup.string().required('Please enter your message'),
});

export default function ContactUs() {
  const { enqueueSnackbar } = useSnackbar();
  const {
    control, formState, handleSubmit, reset,
  } = useForm({
    mode: 'onChange',
    defaultValues,
    resolver: yupResolver(schema),
  });

  const { errors, isValid } = formState;

  const onSubmit = (data: any) => {
    enqueueSnackbar('Message sent', { variant: 'success' });
    console.log({ data });
    reset(defaultValues);
  };

  return (
    <div className='full-width login-container '>
      <div className='login-content full-height flex-col-center'>

        <h1> Drop us a line</h1>

        <form onSubmit={handleSubmit(onSubmit)}>

          <Grid container spacing={1}>
            <Grid item xs={12} md={6}>
              <HookTextField
                name='first_name'
                label='First Name'
                control={control}
                errors={errors}
                type='text'
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <HookTextField
                name='last_name'
                label='Last Name'
                control={control}
                errors={errors}
                type='text'
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <HookTextField
                name='email'
                label='Email'
                control={control}
                errors={errors}
                type='text'
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <HookSelectField
                name='country'
                label='Country'
                control={control}
                errors={errors}
                options={dummyOptions}
              />

            </Grid>
            <Grid item xs={12} md={6}>
              <HookTextField
                name='phone'
                label='Phone'
                control={control}
                errors={errors}
                type='text'
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <HookTextField
                name='compnay'
                label='Compnay (Optional)'
                control={control}
                errors={errors}
                type='text'
              />
            </Grid>
            <Grid item xs={12}>
              <HookTextField
                name='message'
                label='Message'
                control={control}
                errors={errors}
                type='text'
                multiline
                rows={5}
              />
            </Grid>
          </Grid>
          <CustomCheckbox name='isAggreed' label='By checking this box, you agree to receive communications from Strada regarding its products and services offerings.' />

          <div className='contact-us-button'>
            <PrimayButton type='submit' disabled={!isValid}> Contact Us</PrimayButton>
          </div>
        </form>

      </div>

    </div>
  );
}
