import React from 'react';
import './_login.scss';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
import { Link } from 'react-router-dom';
import InputAdornment from '@mui/material/InputAdornment';
import { useMutation } from 'react-query';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import PrimayButton from '../../shared-components/components/PrimayButton';
import HookTextField from '../../shared-components/hooks/HookTextField';
import StradaLoader from '../../shared-components/components/StradaLoader';
import { setLogin } from './store';

function EndAdornment() {
  return (
    <InputAdornment position='end'>
      {' '}
      <Link to='/forget-password' className='forget-button'> Forget? </Link>
      {' '}
    </InputAdornment>
  );
}

type defaultValues = {
    username: string;
    password: string|number;
}

const defaultValues = {
  username: '',
  password: '',
};

const schema = yup.object().shape({
  username: yup.string().required('Please enter your username').email('Your username is not valid').min(6, 'username length can not be less then 6 charecters'),
  password: yup.string().required('Please enter your password'),
});

export default function Login() {
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();
  const {
    control, formState, handleSubmit, reset,
  } = useForm({
    mode: 'onChange',
    defaultValues,
    resolver: yupResolver(schema),
  });

  const { errors, isValid } = formState;

  const handleLogin = (data:defaultValues) => axios({
    url: 'api/auth-users/login/',
    method: 'post',
    data,
  });

  const { mutate, isLoading } = useMutation(handleLogin, {
    onSuccess: (data) => {
      enqueueSnackbar('Login Successfully', { variant: 'success' });
      dispatch(setLogin(data.data));
      reset(defaultValues);
    },
    onError: (error:any) => {
      enqueueSnackbar(error.detail, { variant: 'error' });
    },
  });

  const onSubmit = (data: defaultValues) => {
    mutate(data);
  };

  return (
    <div className='full-width login-container '>
      <div className='login-content full-height flex-col-center'>
        <StradaLoader open={isLoading} />

        <h1> Hi, Welcome Back!</h1>

        <form onSubmit={handleSubmit(onSubmit)}>

          <HookTextField
            name='username'
            label='username'
            control={control}
            errors={errors}
            type='text'
          />
          <HookTextField
            name='password'
            label='Password'
            control={control}
            errors={errors}
            type='password'
            endAdornment={<EndAdornment />}
          />

          <PrimayButton type='submit' disabled={!isValid}> Sign In </PrimayButton>

          <div className='flex-row-center login-link-area'>
            <p className='p-2'> New to Strada? </p>
            <Link to='/signup' className='link-button'> Get Started </Link>
          </div>

        </form>

      </div>

    </div>
  );
}
